.active{
color: goldenrod !important; 
font-weight: bolder;       
}
.deaths{
color: orangered !important; 
font-weight: bolder;      
}
.recovered{
color: green !important; 
font-weight: bolder;          
}
.confirmed{
color: blue !important;
font-weight: bolder;        
}  
.dontShow{
    display:none !important;
}
.titleDisplayer{
    padding-left: 32%;
    padding-top: 2%;    
}
.caseText{
    font-weight: bolder;
}
.cardDisplay{
    width: 55%;        
}
.individualContent{
    display: inline-grid;
    margin:3% !important;    
}
.noShadow{
    box-shadow: none !important;
}
.imageDisplayer{
    width:5.5%
}
@media only screen and (max-width: 800px){
    .active{
        color: goldenrod !important; 
        font-weight: bolder;       
        }
        .deaths{
        color: orangered !important; 
        font-weight: bolder;      
        }
        .recovered{
        color: green !important; 
        font-weight: bolder;          
        }
        .confirmed{
        color: blue !important;
        font-weight: bolder;        
        }    
        .titleDisplayer{
            padding-left: 20%;
            padding-top: 2%;           
        }
        .cardDisplay{
            width: auto;                
        }
        .individualContent{
            display: inline-block;
            padding:5% !important;    
        }
        .imageDisplayer{
            width:15%
        }
  }